




















import Vue from 'vue';
import { io } from 'socket.io-client';

class ChatData {
  name: string;
  content: string;
  time: string;
  id: number;
  constructor(name: string, content: string) {
    this.name = name;
    this.content = content;
    const d = new Date();
    this.time = `${d.getFullYear()}/${
      d.getMonth() + 1
    }/${d.getDate()} ${d.getHours()}:${d.getMinutes()}:${d.getSeconds()}`;
    this.id = d.getTime();
  }
}

export default Vue.extend({
  data: () => ({
    name: '',
    content: '',
    chat: [new ChatData('', '')],
    socket: io(`ws.chattestserveringcp.ml`),
  }),
  computed: {
    canSubmit: function (): boolean {
      return this.name === '' || this.content === '';
    },
  },
  mounted: function () {
    this.socket.on('init', (d: ChatData[]): void => {
      this.chat = d;
    });
    this.socket.on('update', (d: ChatData): void => {
      this.chat.push(d);
    });
  },
  methods: {
    submit: function (): void {
      const d = new ChatData(this.name, this.content);
      this.socket.emit('submit', d);
      this.chat.push(d);
    },
  },
});
